.bookQuote {
  text-align: center;
  margin-top: 10px;
  font-size: 18px;
  font-style: italic;
  color: #6b6969;
  font-family: Spartan;
}

@media only screen and (max-width: 600px) {
  .bookQuote {
    font-size: 13px;
  }
}

.delete-icon {
  font-size: 1.5em;
  align-items: center;
  cursor: pointer;
  color: gray;
}
.hear-icon-fav {
  font-size: 1.1em;
  color: rgb(236, 86, 86);
}

.favorite-header {
  font-family: caladea;
  text-align: center;
  margin-top: 20px;
  font-size: 25px;
}

.book-favorites-container h5 {
  font-family: caladea;
  text-align: center;
  margin-top: 5px;
}

.book-favorites-container h6 {
  font-family: caladea;
  text-align: center;
}

body {
  background-color: #f8f8f8;
}

.nav-bg {
  background-image: url(/static/media/lib.e6aa5c3a.jpg);
  background-position: top;
  background-attachment: fixed;
  background-size: cover;

  box-shadow: 0px 2px 8px rgb(155, 155, 155);
}

.nav-div {
  height: 40vh;
}

.navLink {
  font-family: spartan;
  color: #363636;
  font-weight: bold;
  font-size: 30px;
}

.nav-ul {
  background-color: whitesmoke;
  margin-bottom: 10px;
  box-shadow: 0px 2px 8px rgb(155, 155, 155);
}

.fa-search {
  font-size: 1.5em;
}

.search-box {
  margin-top: 15vh;
  align-items: center;
}

.search-label {
  font-family: "Spartan";
  font-size: 25px;
}

.search-button {
  background-color: #ff0a87;
  box-shadow: 0px 2px 4px rgba(121, 121, 121, 0.616);
  padding: 13px;
  color: white;
  font-weight: 600;
  border-radius: 10px;

  font-size: 14px;
  text-align: center;
  vertical-align: middle;
  margin-top: -6px;
  border: none;
  width: 170px;
  letter-spacing: 1.5px;

  font-family: "Spartan";
}

.search-button:hover {
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.616);
}

.input-row input {
  margin: 3px;
  background-color: white;
}

input[type="text"] {
  background: #f1f1f1;
}

@media only screen and (max-width: 600px) {
  .search-label {
    font-family: "Spartan";
    font-size: 18px;
  }

  .search-button {
    margin-left: 112px;
  }
}

