.book-card-container h6 {
  font-family: "Caladea";
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.8px;
  color: #363636;
  text-align: center;
}

.book-card-container h5 {
  font-weight: 600;
  font-family: "Caladea";
  font-size: 16px;
  color: #363636;
  text-align: center;
  margin-top: 5px;
}
.book-cover {
  margin: auto;
  border-radius: 5px;
  margin-left: 50px;
  box-shadow: 0px 1px 5px rgb(105, 105, 105);
}

.book-card-container {
  margin-top: 15px;
  margin-right: 25px;
  justify-content: space-evenly;
}

.book-card {
  padding: 25px;
  margin: 40px;
  border: 1px solid rgb(224, 221, 221);
  background-color: white;

  /* box-shadow: 0px 2px 5px silver; */
  /* border-radius: 10px; */
  opacity: 0.9;
}

.fa-heart {
  cursor: pointer;
}

.fa-heart:hover {
  color: red;
}

.modal-body-bookCard {
  font-family: "Caladea";
  font-size: 14px !important;
  letter-spacing: 0.5px;
  padding: 20px;
}

.modal-header-bookCard {
  padding: 5px !important;
}

